<template>
  <div>
    <div class="bg-blue-100 flex">
      <div
        class="w-full min-h-screen m-auto bg-slate-100 flex items-center justify-center"
      >
        <div id="snackbar" class="bg-yellow-300">تم اضافة الموعد بنجاح</div>

        <div class="container1">
          <form
            class="main sign-in flex flex-col"
            @submit.prevent="handalSubmit"
          >
            <div class="flex flex-wrap items-center justify-between">
              <div class="card">
                <div class="card-head">
                  <h3 class="header text-teal-700">نهاية وقت الكشف</h3>
                </div>

                <div class="card-body">
                  <div id="frmLogin" class="flex flex-row-reverse flex-wrap">
                    <div class="form-group h-72">
                      <date-picker
                        v-model="time"
                        type="time"
                        placeholder="Select time"
                        valueType="format"
                      ></date-picker>
                    </div>
                  </div>
                </div>
              </div>
              <div class="card">
                <div class="card-head">
                  <h3 class="header text-teal-700">بداية بداية وقت الكشف</h3>
                </div>

                <div class="card-body">
                  <div id="frmLogin" class="flex flex-row flex-wrap">
                    <div class="form-group h-72">
                      <date-picker
                        v-model="time1"
                        type="time"
                        placeholder="Select time"
                        valueType="format"
                      ></date-picker>
                    </div>
                  </div>
                </div>
              </div>
              <div class="card">
                <div class="card-head">
                  <h3 class="header text-teal-700">التاريخ</h3>
                </div>

                <div class="card-body">
                  <div id="frmLogin">
                    <div class="form-group h-72">
                      <date-picker
                        class=""
                        id="date"
                        v-model="date"
                        type="date"
                        placeholder="Select date"
                        valueType="format"
                      ></date-picker>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="form-group">
              <button
                type="submit"
                class="btn bg-teal-700 text-center pb-4"
                id="btnSignIn"
              >
                <span> انشاء</span>
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Swal from "sweetalert2";
import axios from "axios";
import DatePicker from "vue2-datepicker";
import "vue2-datepicker/index.css";
export default {
  data: function () {
    return {
      date: "",
      time: "",
      time1: null,
    };
  },
  components: { DatePicker },
  methods: {
    showAlert(message, icon) {
      Swal.fire({
        title: message,
        icon: icon,
        showConfirmButton: false,
        timer: 2000,
      });
    },
    myFunction() {
      // Get the snackbar DIV
      var x = document.getElementById("snackbar");

      // Add the "show" class to DIV
      x.className = "show";

      // After 3 seconds, remove the show class from DIV
      setTimeout(function () {
        x.className = x.className.replace("show", "");
      }, 3000);
    },
    async handalSubmit() {
      const response = await axios.post(
        "/timeslots/",
        {
          date: this.date,
          time: {
            start_time: this.time1,
            end_time: this.time,
            is_reserved: false,
          },
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("doctoken")}`,
          },
        }
      );
      this.showAlert("تم اضافة الموعد بنجاح", "success");
      // this.myFunction();
      (this.date = ""),
        (this.time = ""),
        (this.time1 = ""),
        console.log(response.data);
      // localStorage.setItem("username", response.data.username);
      // console.log(localStorage.getItem("doctoken"));
    },
  },
};
</script>

